* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent
}

:after, :before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box
}

body {
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "\5FAE\8F6F\96C5\9ED1", Arial, sans-serif;
  font-size: 12px;
  line-height: 1.5;
  color: #515a6e;
  background-color: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale
}

article, aside, blockquote, body, button, dd, details, div, dl, dt, fieldset, figcaption, figure, footer, form, h1, h2, h3, h4, h5, h6, header, hgroup, hr, input, legend, li, menu, nav, ol, p, section, td, textarea, th, ul {
  margin: 0;
  padding: 0
}

button, input, select, textarea {
  font-family: inherit;
  font-size: inherit;
  line-height: inherit
}

input::-ms-clear, input::-ms-reveal {
  display: none
}

a {
  color: #2d8cf0;
  background: 0 0;
  text-decoration: none;
  outline: 0;
  cursor: pointer;
  -webkit-transition: color .2s ease;
  transition: color .2s ease
}

a:hover {
  color: #57a3f3
}

a:active {
  color: #2b85e4
}

a:active, a:hover {
  outline: 0;
  text-decoration: none
}

a[disabled] {
  color: #ccc;
  cursor: not-allowed;
  pointer-events: none
}

code, kbd, pre, samp {
  font-family: Consolas, Menlo, Courier, monospace
}

@font-face {
  font-family: Ionicons;
  src: url(../../static/fonts/ionicons.143146f.woff2) format("woff2"), url(../../static/fonts/ionicons.99ac330.woff) format("woff"), url(../../static/fonts/ionicons.d535a25.ttf) format("truetype"), url(../../static/img/ionicons.a2c4a26.svg#Ionicons) format("svg");
  font-weight: 400;
  font-style: normal
}

.ivu-icon {
  display: inline-block;
  font-family: Ionicons;
  speak: none;
  font-style: normal;
  font-weight: 400;
  font-variant: normal;
  text-transform: none;
  text-rendering: auto;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  vertical-align: middle
}

