  color: #303133;
  font-weight: 700
}
.el-pagination::after, .el-pagination::before {
  display: table;
  content: ""
}
.el-pagination::after {
  clear: both
}
.el-pagination button, .el-pagination span:not([class*=suffix]) {
  display: inline-block;
  font-size: 13px;
  min-width: 35.5px;
  height: 28px;
  line-height: 28px;
  vertical-align: top;
  -webkit-box-sizing: border-box;
